    pointToLayer(feature, latlng) {

      var _this = this;
      var stationstates = "good";
      var stationstatestxt = "";
      var isWarn = feature.properties.isWarn
      var almCount = feature.properties.almCount
      var wrnCount = feature.properties.wrnCount
      var crtCount = feature.properties.crtCount
      var measNm = feature.properties.measNm
      var id = feature.id;
      var VAL = feature.properties.VAL
      var CD = feature.properties.CD

      if (isWarn == true && almCount == 0 && wrnCount == 0 && crtCount != 0) {

        stationstates = "excellent";
        stationstatestxt = "";
      } else if (isWarn == true && almCount == 0 && wrnCount != 0 && crtCount == 0) {

        stationstates = "severe";
        stationstatestxt = "";

      } else if (isWarn == true && almCount != 0 && wrnCount == 0 && crtCount == 0) {

        stationstates = "milder";
        stationstatestxt = "";
      }


      _this.getstInfoByCID(CD)
      _this.getstMeasInfoByCID(CD)
      setTimeout(() => {
        $(".leaflet-popup-tip").addClass(stationstates + "_bg");
        $(".wrapperstats").addClass(stationstates + "_border");
        $(".wrapperstats").text(stationstatestxt);
        $(".icon_wrapper").addClass(stationstates);
        $(".title").addClass(stationstates + "_title");
        $(".row1").addClass(stationstates);
        $(".listdv").addClass(stationstates + "_listdv");


         $(".toolsul>.historyLi").click(function() {

                _this.showhistoryDialogFrom(feature);
       });




      }, 10)


    },



    selectSite(item, index) {

      for (var i = 0; i < this.mapMarkers.length; i++) {
        var marker = this.mapMarkers[i];
        if (marker.feature.properties.CD == item.CD) {
          var popupstr = marker._popup._content;
          var _latlng = marker._latlng;
          var feature = marker.feature;

          var popup = L.popup()
            .setLatLng(_latlng)
            .setContent(popupstr)
            .openOn(this.$refs.cmap.map);

          popup.on('remove', function() {
            $("#panelBottomContainerX").addClass("opacity0").removeClass("animated fadeInUp").addClass("fadeOutDown")

          });




          this.pointToLayer(feature, _latlng);
        }

      }
      this.$refs.cmap.map.setView([item.lttd, item.lgtd], 16);
      this.startup_svg(item.CD);
      this.startup_echarts(item.CD);

    },

    startup_echarts(stcd) {

      this.dataObjecharts = new Object();
      var params = null;
      var _seit = this;
      this.$http.get("./static/iot/site_mgmt/api/site/quote/basic/81211026/81211026.json", {}).then((response) => {
        if (response.data.success === true) {

          var list = response.data.data;
          var flag = 0;
          list.forEach(function(item, index) {
            _seit.dataObjecharts[item.pn] = item.val;
            if (flag == 0) {
              _seit.dataObjecharts['dtmel'] = item.dtmel;
              _seit.dataObjecharts['stnm'] = item.stnm;
            }
            flag++;
          });



          _seit.$http.get("./static/iot/data_monitor/api/monitor-service/stMeasInfo.json", {}).then((response) => {

            var list = response.data.data;

            list.forEach(function(item, index) {

              if (item.NM == '' || item.NM.indexOf('') >= 0) {
                _seit.ST_MEAS_ID = item.ST_MEAS_ID;
              }

            })

            _seit.createChart();

          })



        }
      }).catch((error) => {
        this.$message({
          showClose: true,
          message: error,
          type: 'error'
        });
      });


    },



    startup_svg(stcd) {

      $("#panelBottomContainerX").removeClass("opacity0").removeClass("fadeOutDown").addClass("animated fadeInUp")
      var _seit = this;
      Snap.load("../../static/svg/svgWaterNetMobile.svg", function(f) {
        Snap("#emSvg").clear()
        var g = Snap("#emSvg");
        var g1 = f.select("svg");
        _seit.getData(stcd);
        g.append(g1);
      });


    },


    createChart: function() {
      var _that = this;
      if (!this.chart_1) {
        this.chart_1 = this.$echarts.init(
          document.getElementById("chart" + this.type)
        );



      }

      //48 
      var stm = new Date();
      stm.setTime(stm.getTime() - (1000 * 60 * 60 * 12));
      var param = {
        stcd: this.ST_MEAS_ID,
        stm: stm.toFormatStr(10),
        etm: new Date().toFormatStr(10)
      };

      this.$http.get("./static/iot/grap_chart/station/chart.json", {}).then((response) => {
        if (response.data.success === true) {
          var list = response.data.data;
          _that.listData = _.cloneDeep(list);


          _that.updateChart1(list);
        }

      }).catch((error) => {
        this.$message({
          showClose: true,
          message: error,
          type: 'error'
        });
      });

    },



    updateChart1: function(list) {



      if (this.chart_1) {
        this.chart_1.dispose();
      }
 

      this.chart_1 = this.$echarts.init(
        document.getElementById("chart" + this.type)
      );


      var _that = this;


      var listY = ['', '', '', '', '', ''];
      var listColor = ['#86B4EB', '#F97CBA', '#FFDF98', '#999966', '#2EC7C9', '#AA8463'];
      var listTm = new Array();
      var listZ = new Array();
      var listTop = new Array();
      var listWarn = new Array();
      var listInfo = new Array();
      var listPipe = new Array();
      var listBottom = new Array();

      if (_that.dataObjecharts.dtmel == null || typeof(_that.dataObjecharts.dtmel) == 'undefined')
        _that.dataObjecharts.dtmel = 0;
      var dTop = null;
      if (_that.dataObjecharts.top == null || typeof(_that.dataObjecharts.top) == 'undefined') {
        dTop = null;
      } else {


        dTop = (_that.type == '1' ? _that.dataObjecharts.top : (_that.dataObjecharts.top + _that.dataObjecharts.dtmel));
      }
      var dWarn = null;
      if (_that.dataObjecharts.warn == null || typeof(_that.dataObjecharts.warn) == 'undefined') {
        dWarn = null;
      } else {
        dWarn = (_that.type == '1' ? _that.dataObjecharts.warn : (_that.dataObjecharts.warn + _that.dataObjecharts.dtmel));
      }
      var dPipe = null;
      if (_that.dataObjecharts.pipe == null || typeof(_that.dataObjecharts.pipe) == 'undefined') {
        dPipe = null;
      } else {
        dPipe = (_that.type == '1' ? _that.dataObjecharts.pipe : (_that.dataObjecharts.pipe + _that.dataObjecharts.dtmel));
      }
      var dBottom = (_that.type == '1' ? 0 : _that.dataObjecharts.dtmel);
      var dInfo = null;
      if (_that.dataObjecharts.info == null || typeof(_that.dataObjecharts.info) == 'undefined') {
        dInfo = null;
      } else {
        dInfo = (_that.type == '1' ? _that.dataObjecharts.info : (_that.dataObjecharts.info + _that.dataObjecharts.dtmel));
      }

      for (var i = 0; i < list.length; i++) {
        var model = list[i];
        listTm.push(model.tm);
        var dZ = model.val;
        if (_that.type == '1') {
          //
          //var valInfo=dZ-_that.dataObj.dtmel;
          var valInfo = dZ;
          if (valInfo < 0)
            dZ = 0;
          else
            dZ = valInfo;
        }
        listZ.push(dZ);
        listTop.push(dTop);
        listWarn.push(dWarn);
        listInfo.push(dInfo);
        listPipe.push(dPipe);
        listBottom.push(dBottom);
      }


      var that = this;
      var stnm = that.dataObjecharts.stnm;
      var option = {
        title: {
