          target: 'self',
          x: 'center',
          top: -3,
          textStyle: { fontSize: 12, fontWeight: 'bolder', color: '#333' }
        },
        tooltip: {
          position: function(point, params, dom, rect, size) {
            var width1Mid = dom.parentElement.clientWidth / 2;
            var width2 = dom.clientWidth;
            var x = point[0];
            if (x > width1Mid) {
              x = x - width2 - 20;
            } else {
              x = x + 20
            }
            return [x, 50];
          },
          trigger: 'axis',
          formatter: function(params) {
            var name = '';
            var strInf = '';
            if (params.length > 0) {
              for (var i = 0; i < params.length; i++) {
                if (i == 0) {
                  name = params[i].name;
                }
                var strVal = params[i].value + '';
                if (strVal.indexOf('.') >= 0 && strVal.length > 8) {
                  //
                  try {
                    strVal = Number(strVal).toFixed(2);
                    strVal = Number(strVal);
                  } catch (ex) {}
                }
                if (strVal && Number(strVal) > 0) {
                  strVal = Number(strVal).toFixed(2);
                  strVal = Number(strVal);
                }
                if (strVal == "undefined") {
                  strVal = "--";
                }
                var unit = "";
                if (params[i].seriesName == "" || params[i].seriesName == "" ||
                  params[i].seriesName == "" || params[i].seriesName == "" ||
                  params[i].seriesName == "" || params[i].seriesName == ""
                ) {
                  unit = "(m)";
                } else if (params[i].seriesName == "" || params[i].seriesName == "") {
                  unit = "()";
                } else if (params[i].seriesName == "" || params[i].seriesName == "") {
                  unit = "(%)";
                }

                strInf += '<br/>' + params[i].seriesName + unit + ':' + strVal;
              }
            }
            return name + strInf;
          },
          confine: true,
          padding: 2,
          textStyle: {
            fontSize: 12
          },
          backgroundColor: 'rgba(85, 85, 85, 0.83)'

        },
        legend: {
          data: listY,
          x: 'center',
          y: 'top',
          top: 20
        },
        toolbox: {
          show: false
        },
        grid: {
          top: 45,
          left: '3%',
          right: '4%',
          bottom: '7%',
          containLabel: true
        },
        xAxis: [{
          type: 'category',
          boundaryGap: false,
          data: listTm
        }],
        yAxis: [{
          type: 'value',
          scale: true
        }],
        series: []
      };

      for (var i = 0; i < listY.length; i++) {
        var lineType = "solid";
        if (i != 0) {
          lineType = "dashed";
        }
        var seri = {
          name: listY[i],
          type: 'line',
          showAllSymbol: true,
          smooth: true,
          itemStyle: {
            normal: {
              color: listColor[i],
              lineStyle: { width: 2, type: lineType },
              label: {
                show: false,
                position: 'right',
                textStyle: { fontStyle: 'bolder', fontSize: 12 }
              }
            }
          },
          symbol: 'emptyCircle',
          symbolSize: ((i == 0) ? 2 : 1),
          data: (i == 0 ? listTop : (i == 1 ? listWarn : (i == 2 ? listInfo : (i == 3 ? listPipe : (i == 4 ? listZ : (i == 5 ? listBottom : null))))))
        }
        option.series.push(seri);
      }
      if (this.type == "3") {
        //
        var seri = this.getTempSeri("");
        option.series.push(seri);
        //              option.legend.data.push("");
        var seri = this.getTempSeri("");
        option.series.push(seri);
        //              option.legend.data.push("");

      } else if (this.type == "4") {
        // 
        var seri = this.getTempSeri("");
        option.series.push(seri);
        //              option.legend.data.push("");
        var seri = this.getTempSeri("");
        option.series.push(seri);
        //              option.legend.data.push("");
      }

      this.chart_1.setOption(option);
      window.addEventListener("resize", function() {
        that.chart_1.resize();
      });
      this.loading = false;
    },


    updateChart2: function(list) {


      if (this.chart_2) {
        this.chart_2.dispose();
      }

      this.chart_2 = this.$echarts.init(
        document.getElementById("chart" + this.type)
