    getData(stcd) {
      var _seit = this;
      this.dataObj = new Object();
      this.$http.get("./static/iot/site_mgmt/api/site/quote/basic/81211026/81211026.json", {}).then((response) => {
        if (response.data.success === true) {
          var list = response.data.data;
          var flag = 0;
          list.forEach(function(item, index) {
            _seit.dataObj[item.pn] = item.val;
            if (flag == 0) {

              _seit.dataObj['bottom'] = item.bottom;
            }
            flag++;
          });

          _seit.$http.get("./static/iot/data_monitor/api/monitor-service/stMeasInfo.json", {}).then((response) => {
            var list = response.data.data;
            var dZ = null;
            var mZ = null;
            var ts = '';
            list.forEach(function(item, index) {

              if (item.NM == '' || item.NM.indexOf('') >= 0) {
                var strInfo = item.VAL;
                if (strInfo != null && typeof(strInfo) != 'undefined') {
                  if (strInfo.indexOf('(') >= 0) {
                    strInfo = strInfo.substr(0, strInfo.indexOf('('));
                  }
                  dZ = Number(strInfo) + Number(_seit.dataObj.bottom);
                  mZ = Number(strInfo);
                }
                ts = item.TS;

              }
              //0
              if (dZ == null)
                dZ = 0;
              _seit.dataObj['z'] = dZ;

              //
              if (_seit.dataObj.bottom == null || typeof(_seit.dataObj.bottom) == 'undefined')
                _seit.dataObj.bottom = 0;
              var flaglt = true;
              if (_seit.dataObj.top == null || typeof(_seit.dataObj.top) == 'undefined')
                flaglt = false;
              var flaglp = true;
              if (_seit.dataObj.pipe == null || typeof(_seit.dataObj.pipe) == 'undefined')
                flaglp = false;
              //

              var lTop = (flaglt ? (_seit.dataObj.top + _seit.dataObj.bottom) : '');
              var lPipe = (flaglp ? (_seit.dataObj.pipe + _seit.dataObj.bottom) : '');
              var lBottom = _seit.dataObj.bottom;
              var rTop = _seit.dataObj.top;
              var rPipe = _seit.dataObj.pipe;

              if (mZ < 0)
                mZ = 0;
              _seit.dataObj['lTop'] = lTop;
              _seit.dataObj['lPipe'] = lPipe;
              _seit.dataObj['lBottom'] = lBottom;
              _seit.dataObj['rTop'] = rTop;
              _seit.dataObj['rPipe'] = rPipe;
              _seit.dataObj['mZ'] = mZ;

              _seit.loadData();
              var node = Snap.select("#txtShijian").node;
              node.textContent = ts;
            })



          });





        }

      }).catch((error) => {
        this.$message({
          showClose: true,
          message: error,
          type: 'error'
        });
      });



    },


    loadData() {

      var y1 = 68.76; //SVG
      var y2 = 235.76; //SVG;
      var y3 = 183.76; //SVG

      var y1_ = this.dataObj['rTop']; //
      var y2_ = 0; //0
      var y3_ = this.dataObj['rPipe']; //
      var y4_ = this.dataObj['mZ']; //
      //SVG     SVG y4
      var y4 = y2;
      var y4Height = 0;

      //  
      if (y1_ < y4_ || y3_ > y1_) { //       
        k = (y1_ - y3_) / (y3 - y1);
        var _y4Height = (y1_ - y3_) / k;
        y4Height = y2 - y3 + _y4Height;
        y4 = y2 - y4Height;
      }

      //
      if (0 <= y4_ && y4_ < y3_) { //
        var k = y3_ / (y2 - y3);
        y4Height = y4_ / k;
        y4 = y2 - y4Height;
      }
      if (y3_ <= y4_ && y4_ <= y1_) { //
        k = (y1_ - y3_) / (y3 - y1);
        var _y4Height = (y4_ - y3_) / k;
        y4Height = y2 - y3 + _y4Height;
        y4 = y2 - y4Height;
      }



      var water = Snap.select("#water")
      if (!this.waterY) { //
        this.waterY = Number(water.attr("y"));
        this.waterHeight = Number(water.attr("height"));
      }

      water.animate({
        y: y4,
        height: y4Height
      }, 1000, mina.easein);



      //
      var node = Snap.select("#guandingbiaogao").node;
      if (this.dataObj['lPipe']) {
        this.dataObj['lPipe'] = Number(this.dataObj['lPipe']).toFixed(2);
        this.dataObj['lPipe'] = Number(this.dataObj['lPipe']);
      }
      node.textContent = (this.dataObj['lPipe'] || '') + '(m)';

      //
      var node = Snap.select("#dimianbiaogao").node;

      if (this.dataObj['lTop']) {
        this.dataObj['lTop'] = Number(this.dataObj['lTop']).toFixed(2);
        this.dataObj['lTop'] = Number(this.dataObj['lTop']);
      }

      node.textContent = (this.dataObj['lTop'] || '') + '(m)';
      //
      var node = Snap.select("#jingshen").node;
      if (this.dataObj['rTop']) {
        this.dataObj['rTop'] = Number(this.dataObj['rTop']).toFixed(2);
        this.dataObj['rTop'] = Number(this.dataObj['rTop']);
      }
      node.textContent = (this.dataObj['rTop'] || '') + '(m)';
      //
      var node = Snap.select("#guanding").node;
      if (this.dataObj['rPipe']) {
        this.dataObj['rPipe'] = Number(this.dataObj['rPipe']).toFixed(2);
        this.dataObj['rPipe'] = Number(this.dataObj['rPipe']);
      }
      node.textContent = (this.dataObj['rPipe'] || '') + '(m)';

      //
      var node = Snap.select("#shuiwei").node;
      if (this.dataObj['z']) {
        this.dataObj['z'] = Number(this.dataObj['z']).toFixed(2);
        this.dataObj['z'] = Number(this.dataObj['z']);
      }
      node.textContent = ":" + (this.dataObj['z'] || '') + '(m)';

      //
      var node = Snap.select("#shuishen").node;
      if (this.dataObj['mZ']) {
        this.dataObj['mZ'] = Number(this.dataObj['mZ']).toFixed(2);
        this.dataObj['mZ'] = Number(this.dataObj['mZ']);
      }
      node.textContent = ":" + (this.dataObj['mZ'] || '') + '(m)';

      //
      var node = Snap.select("#jingdibiaogao").node;
      if (this.dataObj['bottom']) {
        this.dataObj['bottom'] = Number(this.dataObj['bottom']).toFixed(2);
        this.dataObj['bottom'] = Number(this.dataObj['bottom']);
      }
      node.textContent = (this.dataObj['bottom'] || '') + '(m)';

      var node = Snap.select("#zGroup");
      var m = new Snap.Matrix();
      m.translate(135, y4 - 2);

      node.animate({ transform: m.toString() }, 1000, mina.easein);
