    createAnnularChart(chartData) {
      let realTime = this.$echarts.init(document.getElementById(chartData.id));
      let option = {
        color: chartData.color,
        title: {
          text: chartData.total,
          textStyle: {
            fontSize: 24
          },
          subtext: chartData.name,
          subtextStyle: {
            fontSize: 15,
            color: '#1a1a1a'
          },
          itemGap: 2,
          x: 'center',
          y: '36%'
        },
        tooltip: {
          show: false
        },
        series: [{
          name: chartData.name,
          type: 'pie',
          center: ['50%', '50%'],
          radius: ['60%', '80%'],
          avoidLabelOverlap: false,
          label: {
            show: false
          },
          labelLine: {
            normal: {
              show: false
            }
          },
          data: chartData.data
        }]
      };
      realTime.setOption(option);
      realTime.on('mouseover', function(params) {
        let title = params.value;
        let subtitle = params.name;
        let index = params.dataIndex;
        realTime.setOption({
          title: {
            text: title,
            subtext: subtitle
          }
        });
        realTime.dispatchAction({
          type: 'highlight',
          seriesIndex: 0,
          dataIndex: index
        });
      });
    },
    realTimeAnnularChart: function() {
      this.realTimeLoading = true;
      this.realTimeNoDataState = false;
      this.$http.get('/Receiving/realDataState', {
        params: {
          mn: this.equipIds.join(",")
        }
      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 0) {
          let list = dataJson.content.dataList;
          let total = 0;
          for (let i = 0; i < list.length; i++) {
            total = total + parseInt(list[i].value);
          }
          let realTimeData = {
            id: 'real-time-monitoring-chart',
            color: ['#24c768', '#ff0000', '#9000ff', '#bdc7d3'],
            data: list,
            total: total,
            name: ''
          };
          if (list.length === 0) {
            this.realTimeNoDataState = true;
          }
          this.createAnnularChart(realTimeData);
          var realarr = [{
            name: '',
            value: list[0].value,
            percent: ((parseInt(list[0].value) / total) * 100).toFixed(1) + '%',
            icon: 'normal'
          }, {
            name: '',
            value: list[1].value,
            percent: ((parseInt(list[1].value) / total) * 100).toFixed(1) + '%',
            icon: 'overproof'
          }, {
            name: '',
            value: list[2].value,
            percent: ((parseInt(list[2].value) / total) * 100).toFixed(1) + '%',
            icon: 'abnormal'
          }, {
            name: '',
            value: list[3].value,
            percent: ((parseInt(list[3].value) / total) * 100).toFixed(1) + '%',
            icon: 'disconnection'
          }];
          this.realTimeLegend = realarr;
        }
        this.realTimeLoading = false;
      }).catch((error) => {
        this.realTimeLoading = false;
        this.realTimeNoDataState = true;
      });
    },
    targetAssessmentChart() {
      this.assessmentLoading = true;
      this.assessmentNoDataState = false;
      this.$http.get('/waterenvapi/realdatas/stationDetail', {
        params: {
          queryType: 'station',
          // startTime:formatDate(new Date(),'yyyy-01-01 00:00:00'),
          // endTime:formatDate(new Date(),'yyyy-MM-dd hh:mm:ss'),
          startTime: new Date().format('yyyy-MM-dd 00:00:00'),
          endTime: new Date().format('yyyy-MM-dd hh:mm:ss'),
          statisType: 'minute'
        }
      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 200) {
          var stations = dataJson.content.dataList;
          if (stations.length === 0) {
            this.assessmentNoDataState = true;
          } else {
            this.assessmentNoDataState = false;
          }
          // var stations = stations;
          var up = 0;
          var down = 0;
          var balance = 0;
          //this.equipIds=[];
          for (var i = 0; i < stations.length; i++) {
            var station = stations[i];
            var level = parseInt(station.waterQualityLevel);
            var target = parseInt(station.targetQualityLevel);
            if (level > target) {
              down++;
            } else if (level < target) {
              up++;
            } else {
              balance++;
            }
          }
          var arr = [];
          arr.push({ value: up, name: '' });
          arr.push({ value: down, name: '' });
          arr.push({ value: balance, name: '' });
          var tarr = [{
            name: '',
            value: up,
            percent: (up / stations.length) * 100,
            icon: 'normal'
          }, {
            name: '',
            value: balance,
            percent: (balance / stations.length) * 100,
            icon: 'disconnection'
          }, {
            name: '',
            value: down,
            percent: (down / stations.length) * 100,
            icon: 'overproof'
          }];
          this.targetAssessment = tarr;
          let realTimeData = {
            id: 'target-assessment',
            color: ['#24c768', '#ff0000', '#bdc7d3'],
            data: arr,
            total: stations.length,
            name: ''
          };
          this.createAnnularChart(realTimeData);
        }
        this.assessmentLoading = false;
      }).catch((error) => {
        this.assessmentLoading = false;
        this.assessmentNoDataState = true;
      });
    },
