    changeSiteType() {
      this.siteLoading = true;
      var _this = this;



      this.$http.get("./static/iot/data_monitor/api/monitor-service/stList.json").then(res => {
          if (res.data.success === true) {
            this.siteList = res.data.data;
            this.siteLoading = false;


            var features = [];
            res.data.data.forEach(function(item, index) {


              var Feature = {
                "type": "Feature",
                "id": index,
                "properties": item,
                "geometry": { "type": "Point", "coordinates": [item.lgtd, item.lttd] }
              };
              features.push(Feature);
            });

            _this.geoJsonData = {
              "type": "FeatureCollection",
              "features": features
            };

            setTimeout(() => {
              this.addClusterLayer(_this.geoJsonData);
            }, 320)

          }



        })
        .catch((error) => {
          this.$message({
            showClose: true,
            message: error,
            type: 'error'
          });

          this.siteLoading = false;
          this.noDataState = true;
        });





