    getstInfoByCID(CD) {

      this.$http.get("./static/iot/data_monitor/api/monitor-service/stInfo.json", {}).then((response) => {
        if (response.data.success === true) {
          $(".Popaddress").text(response.data.data.SITE + "(" + response.data.data.STTP + ")")
          $(".Poptd").text(response.data.data.CRT_DT)
          $(".Popval").text(response.data.data.CRT_DT)

        }

      }).catch((error) => {
        this.$message({
          showClose: true,
          message: error,
          type: 'error'
        });
      });

    },

    getstMeasInfoByCID(CD) {

      this.$http.get("./static/iot/data_monitor/api/monitor-service/stMeasInfo.json", {}).then((response) => {
        if (response.data.success === true) {
          var strs = "";
          response.data.data.forEach(function(item, index) {
            strs = strs + " <div class='rowall'> " + item.NM + ": " + "<div class='  row1'>" + item.VAL + "</div></div>";
          })
          $(".listdv").html(strs)


        }

      }).catch((error) => {
        this.$message({
          showClose: true,
          message: error,
          type: 'error'
        });
      });

    },





    changeSiteType2() {
      this.siteLoading = true;
      //river/area
      this.$http.get('/waterenvapi/realdatas/holeStatis', {
        params: {
          queryType: this.siteTypeGroupValue,
          // startTime:formatDate(new Date(),'yyyy-MM-dd 00:00:00'),
          // endTime:formatDate(new Date(),'yyyy-MM-dd hh:mm:ss'),
          startTime: new Date().format('yyyy-MM-dd 00:00:00'),
          endTime: new Date().format('yyyy-MM-dd hh:mm:ss'),
          statisType: 'minute',
          statusType: this.siteTypeValue
        },


        headers: {
          'Content-Type': 'x-www-from-urlencoded'
        }

      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 200) {
          this.siteList = dataJson.content.dataList;
        }
        this.siteLoading = false;
      }).catch((error) => {
        this.siteLoading = false;
        this.noDataState = true;
      });
    },
    //
    loadStationPoint() {
      this.$http
        .get(
          "/SiteManagement/station/queryAllStationInfo", {
            params: {
              roleId: this.toolObj.getCookie('roleId')
            }
          }).then((response) => {
          let dataJson = response.data;
          if (dataJson.code === 0) {
            var stations = dataJson.content.dataList;
            // this.siteTypeList=dataJson.content.siteTypeList;
            this.mapInfo = [];
            this.equipIds = [];
            for (var i = 0; i < stations.length; i++) {
              var station = stations[i];
              this.equipIds.push(station.stationMn);
              this.mapInfo.push({
                lng: station.lng,
                lat: station.lat,
                name: station.name,
                stationMn: station.stationMn,
                //grade:6,
                grade: parseInt(station.waterQualityLevel),
                wqlindex: "",
                category: station.category,
                time: station.time
              });
            }
          }
        }).catch((error) => {

        });
    },
    // 
    packStatonTypeListVP() {
      this.$http.get('/SiteManagement/station//packStatonTypeListVP', {
        params: {

        }
      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 200) {
          //var stations = dataJson.content.dataList;
          this.siteTypeList = dataJson.content.dataList;


        }
      }).catch((error) => {

      });
    },
    areaEvaluation() { //
      this.$http.get('/waterenvapi/station/realStatisInfo', {
        params: {
          queryType: 'area',
          // startTime:formatDate(new Date(),'yyyy-MM-dd 00:00:00'),
          // endTime:formatDate(new Date(),'yyyy-MM-dd hh:mm:ss'),
          startTime: new Date().format('yyyy-MM-dd 00:00:00'),
          endTime: new Date().format('yyyy-MM-dd hh:mm:ss'),
          statisType: 'minute',
          areaIds: '7E15A844CCB34CCEB42AEE5ED38F5DFC'
        }
      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 200) {
          var station = dataJson.content.dataList[0];
          this.classPollution = station.evaluation; //
          this.classPollutionIcon = this.getClassPollution(station.evaluation); //excellentgoodmildermoderatesevere
          this.majorPollutants = station.primaryPollutant; //
          this.updateTime = station.time;
        }
      }).catch((error) => {

      });
    },
    getRgb16(waterQualityLevel) {
      var level = parseInt(waterQualityLevel);
      if (level == 1) {
        return "#00A1E4";
      } else if (level == 2) {
        return "#00A1E4";
      } else if (level == 3) {
        return "#24C768";
      } else if (level == 4) {
        return "#E5CE10";
      } else if (level == 5) {
        return "#FF7E00";
      } else if (level == 6) {
        return "#FF0000";
      } else {
        return "";
      }
    },
    allAreaStatus() { //
      this.$http.get('/waterenvapi/realdatas/allAreaStatus', {
        params: {
          queryType: 'area',
          statisType: 'minute',
          pareaIds: '7E15A844CCB34CCEB42AEE5ED38F5DFC'
        }
      }).then((response) => {
        let dataJson = response.data;
        if (dataJson.code === 200) {
          var list = dataJson.content.dataList;
          var str = "";
          for (var i = 0; i < list.length; i++) {
            var info = list[i];
            str = str + '"' + info.area + '":"' + this.getRgb16(info.waterQualityLevel) + '",';
          }
          str = "{" + str.substring(0, str.length - 1) + "}";
          this.$refs.cmap.quZColorObj = JSON.parse(str);

        }
      }).catch((error) => {

      });
    },
    controlLevel(level) {
      var levelName = "";
      if (level == 0) {
        levelName = "";
      } else if (level == 1) {
        levelName = "";
      } else if (level == 2) {
        levelName = "";
      } else if (level == 3) {
        levelName = "";
      } else if (level == 4) {
        levelName = "";
      }
      return levelName;
    },
    convertDateFromString(dateString) {
      if (dateString) {
        var date = new Date(dateString.replace(/-/, "/"))
        return date;
      }
    },
    getClassWarn(isWarn, almCount, wrnCount, crtCount) {
      var stationstates = "good";

      if (isWarn == false) {
        stationstates = "good";
      } else if (isWarn == true && almCount == 0 && wrnCount == 0 && crtCount != 0) {
        stationstates = "excellent";
      } else if (isWarn == true && almCount == 0 && wrnCount != 0 && crtCount == 0) {
        stationstates = "severe";
      } else if (isWarn == true && almCount != 0 && wrnCount == 0 && crtCount == 0) {
        stationstates = "milder";
      } else {
        stationstates = "";
      }
      return stationstates;

    },
    getClassPollution(eva, time) {
      //excellentgoodmildermoderatesevere
      if (time) {
        var date = new Date();
        var cdate = this.convertDateFromString(time);
        var dbvalue = date.getTime() - cdate.getTime();
        // dbvalue=Math.abs(dbvalue/1000);
        dbvalue = dbvalue / 1000;

        if (dbvalue > 3600 * 8) {
          return "lixian";
        }
      }
      var pollution = "good";
      if (eva == "") {
        pollution = "excellent";
      } else if (eva == "") {
        pollution = "good";
      } else if (eva == "") {
        pollution = "milder";
      } else if (eva == "") {
        pollution = "moderate";
      } else if (eva == "") {
        pollution = "severe";
      } else {
        pollution = "";
      }
      return pollution;
    },
    getEvaluateText(eva) {
      var rtn = "";
      if (eva == '~') {
        rtn = "";
      } else if (eva == '') {
        rtn = "";
      } else if (eva == '') {
        rtn = "";
      } else if (eva == '') {
        rtn = "";
      } else if (eva == '') {
        rtn = "";
      }
      return rtn;
    },
    waterQualityLevel(eve, time, level) {
      var cls = this.getClassPollution(eve, time);
      if (cls == "lixian") {
        return "-";
      }
      if (level <= 1) {
        return "";
      } else if (level <= 2) {
        return "";
      } else if (level <= 3) {
        return "";
      } else if (level <= 4) {
        return "";
      } else if (level <= 5) {
        return "";
      } else if (level <= 6) {
        return "";
      } else {
        return "-";
      }
    },
    setWaterQualityLevel(eve, time, type) {
      var cls = this.getClassPollution(eve, time);
      if (cls == "lixian") {
        return "";
      }
      let level = parseFloat(type);
      if (level === 1) {
        return "typeA";
      } else if (level === 2) {
        return "typeA";
      } else if (level === 3) {
        return "typeB";
      } else if (level === 4) {
        return "typeC";
      } else if (level === 5) {
        return "typeD";
      } else if (level === 6) {
        return "typeE";
      } else {
        return "";
      }
    },
    siteType(type) {
      return "auto";
    },
    siteStatus(status) {
      //onLineoffLineoverproof
      if (status == 1) {
        return "onLine";
      } else {
        return "offLine";
      }
    },
    getrealtime() {
      // return ''+formatDate(new Date(),'yyyy-MM-dd hh:00');
      return '' + new Date().format('yyyy-MM-dd hh:00');
    },
  },
  mounted() {

    setTimeout(() => {
      $(".wrap li").click(function() {

        $(this).siblings('li').removeClass('active'); // 
        $(this).addClass('active'); // 
      });
    }, 10)




    // this.waterQualityChart();
    //this.realTimeAnnularChart();
    this.changeSiteType();


    //  this.loadStationPoint();
    //this.packStatonTypeListVP();



    //  this.areaEvaluation();
    //this.targetAssessmentChart();
    //this.allAreaStatus();
    this.pagerefresh();

    document.addEventListener('click', () => {
      this.menuListToggle = false;
      this.menuPanelToggle = false;
    });

  },
  watch: {
    siteTypeGroupValue: function() {
      this.changeSiteType();
    },
    siteList: function(val) {
      if (val.length === 0) {
        this.noDataState = true;
      } else {
        this.noDataState = false;
      }
    },
    realTimeLegend: function(val) {
      if (val.length === 0) {
        this.realTimeNoDataState = true;
      } else {
        this.realTimeNoDataState = false;
      }
    }
  },
  components: {
    
    waterRanking,
    trendAnalysis,
    siteDetails,
    mapComp,
    realTime,
    waterQualityBulletin,
    historicalData,
  }
}

</script>
