      background: rgba(255, 255, 255, 0.4);
    }

    .prediction-play-fun>.time-line {
      background: #3ace7e;
    }

    .prediction-time-wrap .prediction-time-line {
      display: block;
      height: 6px;
      background: url("../../assets/images/index/prediction-time-line.png") repeat-y;
      background-size: 100% 1px;
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      transition: all 0.3s;
    }
  }
}

