/**/
.real-time-monitoring {
  height: 190px;
  position: relative;
}

#real-time-monitoring-chart,
#target-assessment {
  float: left;
  width: 188px;
  height: 190px;
}

.monitoring-chart-legend {
  width: 235px;
  float: left;
  padding-top: 26px;

  li {
    border: 1px solid #d6e3f2;
    height: 30px;
    line-height: 28px;
    border-radius: 15px;
    background: #e3edf8;
    padding: 0 12px;
    margin-bottom: 6px;

    .legend-icon {
      display: inline-block;
      vertical-align: middle;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      border: 2px solid blue;
      background: #fff;
      margin-bottom: 3px;
      margin-right: 10px;
    }

    .legend-icon.normal {
      border-color: #24c768;
    }

    .legend-icon.overproof {
      border-color: #ff0000;
    }

    .legend-icon.abnormal {
      border-color: #9207ff;
    }

    .legend-icon.disconnection {
      border-color: #bec8d4;
    }

    .legend-name {
      display: inline-block;
      vertical-align: top;
      width: 62px;
    }

    .legend-number {
      display: inline-block;
      vertical-align: top;
      width: 44px;
    }
  }
}

.monitoring-chart-legend.target-assessment-legend {
  padding-top: 32px;

  li {
    margin-bottom: 16px;
  }
}

/*  */
.site-data-header {
  line-height: 32px;
  position: relative;
  display: inline-block;
  padding-left: 30px;

  i.icon-baojing {
    font-size: 16px;
    color: #ff0f0f;
    animation: fabell 1.2s infinite;
    position: absolute;
    left: 10px;
  }

  .site-title-name {
    font-size: 16px;
    font-weight: 700;
    color: #505e66;
  }

  .give-alarm-text {
    color: #999;
  }

  .site-extra-function {
    display: inline-block;
    vertical-align: top;
    margin-left: 16px;

    button {
      height: 32px;
      border: 1px solid #fff;
      padding: 0 10px;
      color: #166bce;
      cursor: pointer;

      i {
        font-size: 13px;
      }
    }

    button:hover {
      border: 1px solid #b7d7f7;
      background: #e6f1fc;
    }
  }
}

@keyframes fabell {
  0% {
    margin-left: 0;
  }

  5% {
    margin-left: 1px;
  }

  8% {
    margin-left: -1px;
  }

  10% {
    margin-left: 1px;
  }

  13% {
    margin-left: -1px;
  }

  15% {
    margin-left: 1px;
  }

  18% {
    margin-left: -1px;
  }

  20% {
    margin-left: 1px;
  }

  23% {
    margin-left: -1px;
  }

  25% {
    margin-left: 1px;
  }

  28% {
    margin-left: -1px;
  }

  30% {
    margin-left: 1px;
  }

  33% {
    margin-left: -1px;
  }

  35% {
    margin-left: 1px;
  }

  40% {
    margin-left: 0;
  }

  45% {
    margin-left: 0;
  }

  50% {
    margin-left: 0;
  }

  60% {
    margin-left: 0;
  }

  70% {
    margin-left: 0;
  }

  80% {
    margin-left: 0;
  }

  90% {
    margin-left: 0;
  }

  100% {
    margin-left: 0;
  }
}

/*  */
.regional-enter,
.regional-leave-to,
.weather-enter,
.weather-leave-to {
  opacity: 0;
  transform: translate(0, 20px);
}

.regional-enter-active,
.regional-leave-active,
.weather-enter-active,
.weather-leave-active {
  transition: all 0.3s;
}

.regional-enter-to,
.regional-leave,
.weather-enter-to,
.weather-leave {
  opacity: 1;
  transform: translate(0, 0);
}

.el-dialog-title {
  line-height: 24px;
  font-size: 16px;
  color: #505e66;
  padding-top: 10px;
}

.el-dialog-subtitle {
  color: #505e66;
  font-size: 13px;
  line-height: 24px;
  padding-bottom: 10px;
  font-weight: 100;
}

.search-data-btn {
  display: inline-block;
  margin-left: 14px;
  text-decoration: underline;
  color: #1875d0;
  cursor: pointer;
}

.search-data-btn:hover {
  color: #1061b1;
}

/*  */
.map-comp-toolbar {
  position: absolute;
  z-index: 999;
  top: 16px;
  right: 370px;
  transition: all 0.3s;

  .normal-toolbar {
    background: #ffffff;
    border-radius: 3px;
    padding: 0;
    box-shadow: 0 2px 7px #cdcdc9;
    float: left;
    margin-right: 10px;

    &>li {
      float: left;
      padding: 10px 16px;
      cursor: pointer;
      position: relative;

      &>i {
        margin-right: 4px;
        color: #849c9f;
      }

      &:after {
        content: '';
        width: 1px;
        height: 14px;
        background: #cacaca;
        position: absolute;
        top: 13px;
        right: 0;
      }

      &:last-child {
        &:after {
          display: none;
        }
      }

      &:hover {
        color: #166bce;

        i {
          color: #166bce;
        }
      }
    }
  }

  .map-type-toolbar {
    float: left;
    position: relative;

    .type-label {
      background: #fff;
      box-shadow: 0 2px 7px #cdcdc9;
      border-radius: 3px;
      height: 39px;
      line-height: 39px;
      padding: 0 13px;
      cursor: pointer;

      &>span {}
    }

    .map-type-panel {
      position: absolute;
      right: 0;
      top: calc(100% + 2px);
    }

    .el-radio-group {
      background: #fff;
      box-shadow: 0 2px 7px #cdcdc9;
      border-radius: 3px;
      padding: 10px 14px;

      .group-item {
        padding: 6px 0;
      }
    }

    .type-icon {
      display: inline-block;
      vertical-align: middle;
      width: 22px;
      height: 22px;
    }

    .vectorgraph-icon {
      background: url("../../assets/images/index/shiliangtu_icon.png") no-repeat;
    }

    .topographic-icon {
      background: url("../../assets/images/index/dixingtu_icon.png") no-repeat;
    }

    .image-map-icon {
      background: url("../../assets/images/index/yingxiangtu_icon.png") no-repeat;
    }
  }

  .zoom-in-toolbar {
    position: absolute;
    right: 0;
    top: 52px;
  }

  .zoom-box {
    background: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 7px #cdcdc9;
    padding: 0 5px;
    margin-bottom: 12px;
    cursor: pointer;

    .zoom-item {
      font-size: 20px;
      color: #646464;
      border-bottom: 1px solid #dfdfdf;
      line-height: 36px;
      padding: 0 2px;

      &:last-child {
        border-bottom: none;
      }

      &:hover {
        color: #0091ff;

        .location-icon {
          opacity: 1;
        }
      }
    }

    .location-icon {
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 2px;
      width: 20px;
      height: 21px;
      background: url("../../assets/images/index/pointPosition.png") no-repeat center center;
      opacity: 0.7;
    }
  }
}

.toggle-panel.map-comp-toolbar {
  right: 16px;
}

/*  */
.situation-statistics {
  position: absolute;
  bottom: 16px;
  right: 360px;
  z-index: 999;
  transition: all 0.3s;

  .statistics-panel {
    position: absolute;
    right: 0;
    bottom: 0;
    background: #fff;
    width: 394px;
    height: 178px;
    border: 1px solid #8b9bac;
    transition: 0.3s;
    overflow: hidden;

    .minimize-btn {
      position: absolute;
      top: 0;
      right: 0;
      cursor: pointer;
      width: 30px;
      height: 30px;
      line-height: 30px;
      text-align: center;

      &:hover {
        background: #eee;
      }

      &:active {
        background: #ccc;
      }
    }

    .panel-header {
      height: 30px;
      line-height: 30px;
      padding-left: 16px;
      border-bottom: 1px solid #dce9fa;

      .title-icon {
        display: inline-block;
        vertical-align: middle;
        width: 20px;
        height: 18px;
        font-size: 12px;
        background: url("../../assets/images/index/benyue_icon.png") no-repeat;
      }
    }

    .panel-body {
      height: calc(100% - 30px);

      .situation-list {
        &>li {
          float: left;
          width: 33.3%;
          padding: 12px 0 0 16px;

          .name {
            color: #999999;
          }

          .number {
            padding: 10px 0;

            &>i {
              display: inline-block;
              vertical-align: middle;
              margin-bottom: 3px;
              width: 23px;
              height: 23px;
              margin-right: 4px;
            }

            &>span {
              font-size: 16px;
              font-weight: 700;
            }

            .fault-icon {
              background: url("../../assets/images/index/fashengguzhang_icon.png") no-repeat;
            }

            .consumable-parts-icon {
              background: url("../../assets/images/index/xiaohaopeijian_icon.png") no-repeat;
            }

            .inspection-icon {
              background: url("../../assets/images/index/xunjian_icon.png") no-repeat;
            }
          }

          .situation-text {
            color: #666666;
            font-size: 12px;
            line-height: 28px;

            &:before {
              content: '';
              display: inline-block;
              vertical-align: middle;
              margin-bottom: 3px;
              width: 6px;
              height: 6px;
              background: #c7d8ee;
              margin-right: 8px;
            }
          }
        }
      }
    }
  }

  .statistics-panel.hide {
    width: 0;
    height: 0;
  }

  .statistics-icon {
    position: absolute;
    cursor: pointer;
    right: -4px;
    bottom: -6px;
    width: 47px;
    height: 49px;
    background: url("../../assets/images/index/tongji_nor_btn.png") no-repeat;

    &:hover {
      background: url("../../assets/images/index/tongji_hover_btn.png") no-repeat;
    }

    &:active {
      background: url("../../assets/images/index/tongji_link_btn.png") no-repeat;
    }
  }
}

.toggle-panel.situation-statistics {
  right: 16px;
}

/*  */
.shortcut-menu-wrap {
  position: absolute;
  top: 14px;
  left: 14px;
  z-index: 999;

  .menu-btn-wrap {
    background: #ffffff;
    border-radius: 3px;
    box-shadow: 0 2px 7px #cdcdc9;
    padding: 5px 10px;

    &>a {
      display: inline-block;
      vertical-align: top;
      color: #333333;
      height: 25px;
      line-height: 25px;
      border: 1px solid #bfc7ca;
      padding: 0 8px;
      border-radius: 3px;
      margin-right: 2px;
      /* Safari 5.1 - 6.0 */
      background: -webkit-repeating-linear-gradient(#fff, #fff 50%, #ededed 50%, #ededed 100%);
      /* Opera 11.1 - 12.0 */
      background: -o-repeating-linear-gradient(#fff, #fff 50%, #ededed 50%, #ededed 100%);
      /* Firefox 3.6 - 15 */
      background: -moz-repeating-linear-gradient(#fff, #fff 50%, #ededed 50%, #ededed 100%);
      /*  */
      background: repeating-linear-gradient(#fff, #fff 50%, #ededed 50%, #ededed 100%);

      i {
        transition: all 0.3s;
      }
    }

    &>a:last-child {
      margin-right: 0;
    }

    &>.toggle-btn {
      padding: 0 6px;
    }

    &>a:hover {
      color: #166bce;
      border: 1px solid #166bce;
    }

    &>a.active {
      color: #166bce;
      border: 1px solid #166bce;
      /* Safari 5.1 - 6.0 */
      background: -webkit-repeating-linear-gradient(#fff, #fff 50%, #dfe6ed 50%, #dfe6ed 100%);
      /* Opera 11.1 - 12.0 */
      background: -o-repeating-linear-gradient(#fff, #fff 50%, #dfe6ed 50%, #dfe6ed 100%);
      /* Firefox 3.6 - 15 */
      background: -moz-repeating-linear-gradient(#fff, #fff 50%, #dfe6ed 50%, #dfe6ed 100%);
      /*  */
      background: repeating-linear-gradient(#fff, #fff 50%, #dfe6ed 50%, #dfe6ed 100%);
    }

    &>a.active.toggle-btn {
      i {
        transform: rotate(-180deg);
      }
    }
  }

  /*  */
  .menu-list {
    position: absolute;
    top: 42px;
    left: 0;
    background: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 7px #cdcdc9;
    /*padding: 12px 15px;*/
    height: 260px;

    .add-menu {
      padding: 12px 15px 0;
    }

    .added-menu {
      padding: 0 15px 6px;
    }

    .menu-title {
      line-height: 26px;
      width: 230px;

      .name {
        width: 206px;
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;

        &:before {
          content: '';
          display: inline-block;
          vertical-align: middle;
          margin-bottom: 3px;
          width: 13px;
          height: 13px;
          background: url("../../assets/images/list-icon.png") no-repeat;
          margin-right: 6px;
        }
      }

      .toggle-btn {
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;
        width: 18px;
        height: 18px;
        border: 1px solid #e8e8e8;
        text-align: center;
        line-height: 18px;
        cursor: pointer;

        &>i {
          vertical-align: top;
          margin-top: 2px;
          transition: all 0.3s;
          transform: rotate(180deg);
        }

        &:hover {
          color: #166bce;
          background: #e0eaf5;
        }
      }

      .toggle-btn.active {
        &>i {
          transform: rotate(0deg);
        }
      }
    }

    .menu-operation {
      width: 230px;
      padding-left: 20px;
      padding-bottom: 4px;

      .list-item {
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;
        width: 174px;
      }

      .operation-btn {
        cursor: pointer;

        &>i {
          font-size: 16px;
          color: #4f93e1;
        }

        &:hover {
          i {
            color: #166bce;
          }
        }
      }

      &>li {
        line-height: 28px;
      }

      &>li:before {
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;
        width: 4px;
        height: 4px;
        border-radius: 50%;
        background: #b4c2d6;
        margin-right: 8px;
      }
    }
  }

  /*  */
  .menu-panel {
    position: absolute;
    top: 42px;
    left: 0;
    background: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 7px #cdcdc9;
    z-index: 999;
    height: 200px;
    width: 300px;

    .menu-item-list {
      padding: 16px 16px 0;

      &>li {
        float: left;
        width: 33.3%;
        text-align: center;
        color: #666666;
        margin-bottom: 10px;
        cursor: pointer;

        .item-icon {
          display: block;
          margin: 0 auto;
          width: 42px;
          height: 42px;
          opacity: 0.7;
          transition: all 0.3s;
        }

        .item-name {
          line-height: 36px;
        }
      }

      &>li:hover {
        color: #166bce;

        .item-icon {
          opacity: 1;
        }
      }
    }
  }

  .smallPanel.menu-panel {
    height: 104px;
  }
}

.water-quality {
  display: inline-block;
  vertical-align: middle;
  width: 18px;
  height: 18px;
  margin-bottom: 3px;
}

.typeA {
  background: url("../../assets/images/water_quality_01.png") no-repeat;
  background-size: 100% 100%;
}

.typeB {
  background: url("../../assets/images/water_quality_02.png") no-repeat;
  background-size: 100% 100%;
}

.typeC {
  background: url("../../assets/images/water_quality_03.png") no-repeat;
  background-size: 100% 100%;
}

.typeD {
  background: url("../../assets/images/water_quality_04.png") no-repeat;
  background-size: 100% 100%;
}

.typeE {
  background: url("../../assets/images/water_quality_05.png") no-repeat;
  background-size: 100% 100%;
}

</style>



