        var b, c, d, e, f, g, h, i, j, k;
        _this.RainImgLayer.clearLayers();
         b = JSON.parse(a.contours);
 

        for (  c = 0; c < b.length; c++) {
          for (d = [], e = b[c], f = 0; f < e.latAndLong.length; f++) d.push([e.latAndLong[f][0], e.latAndLong[f][1]]);
          g = b[c].color.substring(0, b[c].color.lastIndexOf(",")),
          h = L.polygon(d, {
            fillOpacity: .5,
            color: "rgb(" + g + ")",
            weight: 0
          }) .bindTooltip(_this.rainLevel[e.symbol], {permanent: false, className: "my-label", offset: [0, 10] })
            .addTo(_this.RainImgLayer)
        }
        _this.RainImgLayer.addTo(_this.map)  
     //   console.log(_this.map.setZoom(2))

        gismap.setmaploading(false)
      
        
         
      }
    })
  } catch(b) {
