    //
    geolocationSelf() {
      let _this = this;
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(function(position) {
          x = position.coords.longitude;
          y = position.coords.latitude;
          console.log("2" + y + "" + x);
        }, (err) => {
          console.log(err);
        }, { timeout: 60000 });
      } else {
        this.$message('');
      }
