      } else if (type == "out") {
        this.map.zoomOut();
      }
    },
    /**
     *  
     */
    meatureTool(type) {
      switch (type) {
        case "polyline":
          this.meatures.drawController("polyline");
          break;
        case "polygon":
          this.meatures.drawController("polygon");
          break;
        case "point":
          this.meatures.drawController("point");
          break;
        case "circle":
          this.meatures.drawController("circle");
          break;
        default:
          break;
      }
