<template>
    <div class="site-details-info">
      <ul class="site-link-list">
        <li
          :class="{active: index === selectIndex}"
          :key="index"
          v-for="(item,index) in siteDetailsList"
          @click="tabToggle(item,index)"
        >{{ item.name }}
        </li>
      </ul>
      <ul>
        <li v-if="selectIndex === 0">
          <div class="site-info-wrap base-content">
            <el-scrollbar style="height: 100%;">
              <table class="normal-table">
                <tr>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>A410800_0002</td>
                  <th></th>
                  <td>647604</td>
                </tr>
                <tr>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>--</td>
                </tr>
                <tr>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>10-</td>
                </tr>
                <tr>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>10-</td>
                </tr>
                <tr>
                  <th></th>
                  <td>35.311814-112.121327</td>
                  <th></th>
                  <td>916</td>
                  <th></th>
                  <td>-</td>
                </tr>
                <tr>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>2019-05-05</td>
                  <th></th>
                  <td></td>
                </tr>
                <tr>
                  <th></th>
                  <td>-</td>
                  <th></th>
                  <td></td>
                  <th></th>
                  <td>--</td>
                </tr>
                <tr>
                  <th></th>
                  <td colspan="5">--</td>
                </tr>
                <tr>
                  <th></th>
                  <td colspan="5">
                    
                    201210201212
                    272297M--
                    ,169Km,3152
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td colspan="5">
                    
                    201210201212
                    272297M--
                    ,169Km,3152
                  </td>
                </tr>
              </table>
            </el-scrollbar>
          </div>
        </li>
        <li v-if="selectIndex === 1">
            <div class="instrument-param">
              
            </div>
        </li>
        <li v-if="selectIndex === 2">
          <div class="site-image clear">
            <div class="big-image">
              <img :src="siteImage.img" :alt="siteImage.name" :title="siteImage.name">
            </div>
            <div class="small-image-wrap base-content">
              <el-scrollbar style="height: 100%">
                <ul>
                  <li v-for="(item,index) in siteImage.list"
                      :class="{active: siteImage.active === index}"
                      @click="toggleImage(item,index)"
                      :key="index">
                    <img :src="item.img" :alt="item.time" :title="item.time">
                    <p>{{ item.time }}</p>
                  </li>
                </ul>
              </el-scrollbar>
            </div>
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "siteDetailsInfo",
        data(){
          return{
            selectIndex: 0,
            siteDetailsList:[{
              name:'',
              src:'siteInfo'
              },{
                name:'',
                  src:'instrumentParam'
              },{
                name:'',
                  src:'siteImage'
              }],
            siteImage:{
              img:'./static/images/siteImage/site-image.png',
              active:0,
              name:'2019-05-04 10:00',
              list:[{
                img:'./static/images/siteImage/site-image.png',
                time:'2019-05-04 10:00'
              },{
                img:'./static/images/siteImage/site-image.png',
                time:'2019-05-04 11:00'
              },{
                img:'./static/images/siteImage/site-image.png',
                time:'2019-05-04 12:00'
              },{
                img:'./static/images/siteImage/site-image.png',
                time:'2019-05-04 13:00'
              },{
                img:'./static/images/siteImage/site-image.png',
                time:'2019-05-04 14:00'
              }]
            }
          }
        },
        methods:{
          //tab
          tabToggle(item,index){
            this.selectIndex = index;
          },
          //
          toggleImage(item,index){
            this.siteImage.img = item.img;
            this.siteImage.active = index;
            this.siteImage.name = item.time;
          },
        }
    }
</script>

<style scoped lang="less">
.site-details-info{

}
.site-link-list{
  height: 32px;
  line-height: 30px;
  border-bottom: 1px solid #d6e3f2;
  li{
    width: calc(100% / 6);
    float: left;
    text-align: center;
    border-right: 1px solid #d6e3f2;
    position: relative;
    cursor: pointer;
  }
  /*li:first-child{
    border-left: none;
  }*/
  li:hover{
    background: #eaeff4;
  }
  li.active{
    color: #166bce;
    border-bottom: 1px solid #fff;
  }
  li.active:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background: #166bce;
  }
}
.site-info-wrap{
  height: 466px;
  padding: 8px;
}
.normal-table{
  th,td{
    height: auto;
    padding: 6px 8px;
  }
  th{
    width: 80px;
  }
  td{
    width: calc(33.3% - 82px);
    line-height: 22px;
  }
}
.instrument-param{
  height: 466px;
}
.site-image{
  height: 466px;
  padding: 16px;
  .big-image{
    border: 3px solid #d6e3f2;
    width: calc(100% - 188px);
    height: 100%;
    float: left;
    &>img{
      height: 100%;
      width: 100%;
    }
  }
  .small-image-wrap{
    float: right;
    width: 178px;
    border: 1px solid #d6e3f2;
    height: 100%;
    ul{
      padding: 8px;
      &>li{
        height: 100px;
        border: 2px solid #cbdef2;
        position: relative;
        margin-bottom: 8px;
        cursor: pointer;
        &:last-child{
          margin-bottom: 0;
        }
        &:hover{
          border-color: #11a4fb;
        }
        &>p{
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          padding-left: 8px;
          font-size: 12px;
          color: #fff;
        }
        &>img{
          width: 100%;
          height: 100%;
        }
      }
      &>li.active{
        border-color: #11a4fb;
        &:before{
          content: '';
          position: absolute;
          top: 50%;
          margin-left: -5px;
          left: -12px;
          border: 8px solid transparent;
          border-right-color: #11a4fb;
        }
      }
    }
  }
}
</style>



