<template>
    <!-- = -->
  <div class="real-time-param">
    <el-table
      :data="tableData"
      height="100%"
      border
      style="width: 100%">
      <el-table-column
        prop="instrument"
        label="">
      </el-table-column>
      <el-table-column
        prop="parameter"
        label="">
      </el-table-column>
      <el-table-column
        prop="company"
        label="">
      </el-table-column>
      <el-table-column
        prop="state"
        label="">
      </el-table-column>
      <el-table-column
        prop="currentValue"
        label="">
      </el-table-column>
      <el-table-column
        prop="normalRange"
        label="">
      </el-table-column>
      <el-table-column
        prop="uploadTime"
        label="">
      </el-table-column>
      <el-table-column
        prop="modificationRecord"
        label="">
      </el-table-column>
      <el-table-column
        prop="operation"
        label="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
    export default {
        name: "realTimeParam",
        data(){
          return{
            tableData:[]
          }
        }
    }
</script>

<style scoped lang="less">
.real-time-param{
  height: 466px;
  padding: 7px;
}
</style>



