<template>
    <div class="overview-control">
        <el-scrollbar style="height: 100%">
          <div style="padding: 7px;">
            <div class="modular-wrap">
              <div class="modular-header">
                <span class="title"></span>
                <a class="link-btn">  >> </a>
              </div>
              <div class="modular-body">
                <div class="modular-row clear">
                  <div class="base-info">
                    <div class="base-title">
                      <i class="base-site-icon"></i>
                      <span class="base-site-name">
                        {{ siteInfo.stationName }}
                      </span>
                      <a href="javascript:;" class="details-ben" @click="siteDetails = true;"></a>
                    </div>
                    <ul class="base-info-list">
                      <li>
                        <p>&emsp;&emsp;
                          {{ siteInfo.stationName }}
                        </p>
                        <p>
                          {{ siteInfo.basinn.basinnName }}
                        </p>
                      </li>
                      <li>
                        <p>
                          {{siteInfo.buildTime }}
                        </p>
                        <p>
                          {{  siteInfo.area.areaName}}
                        </p>
                      </li>
                      <li>
                        <p>{{ siteInfo.stationtype }}</p>
                        <p><i class="target-level"></i></p>
                      </li>
                    </ul>
                  </div>
                  <div class="base-img">
                    <img src="../../assets/images/index/site-icon.png" />
                  </div>
                </div>
                <div class="modular-row clear">
                  <div class="secondary-title"></div>
                  <div class="secondary-menu">
                    <span class="item"></span>
                    <span class="item"></span>
                    <span class="item"></span>
                    <span class="item"></span>
                    <span class="item"></span>
                    <span class="item">PH</span>
                    <span class="item"></span>
                    <span class="item"></span>
                    <span class="item"></span>
                  </div>
                </div>
              </div>
            </div>
            <div class="modular-wrap">
              <div class="modular-header">
                <span class="title"></span>
              </div>
              <div class="modular-body" style="padding: 0">
                <div class="monitoring-info">
                  <div class="monitoring-title">
                    <span class="title"><i class="abnormal-icon"></i> </span>
                    <span class="title"><i class="site-normal-icon"></i> </span>
                    <span class="title"><i class="site-offline-icon"></i> </span>
                    <div class="toolbar">
                      <el-button type="secondBtn"></el-button>
                    </div>
                  </div>
                  <div class="monitoring-text">
                    <span>PH</span>
                    <span><i class="water-quality typeC"></i> </span>
                    <span><i class="water-quality typeC"></i> </span>
                  </div>
                </div>
                <div class="audit-status">
                  <div class="audit-title">
                    <span class="title"><i class="audit-icon"></i> </span>
                  </div>
                  <div class="audit-wrap">
                    <span class="audit-item"><i class="state-icon state-normal"></i> 10:00:00</span>
                    <span class="audit-item"><i class="state-icon"></i> </span>
                    <span class="audit-item"><i class="state-icon"></i> </span>
                  </div>
                </div>
                <div class="audit-data-wrap">
                  <div class="audit-data-title">
                    <span class="title">2018-05-09 10:20:20</span>
                    <div class="toolbar">
                      <el-button type="secondBtn" @click="auditDate"></el-button>
                    </div>
                  </div>
                  <div class="audit-data-content">
                    <ul class="audit-data-list clear">
                      <li v-for="(item,index) in auditData">
                        <p><span class="value">{{ item.value }}</span><span class="company">{{ item.company }}</span></p>
                        <div class="audit-text">
                          <span>{{ item.factor }}</span>
                          <el-tooltip class="item" effect="dark" :content="setGradeClass(item,'val')" placement="top">
                            <i class="grade-icon" :class="setGradeClass(item,'cls')"></i>
                          </el-tooltip>
                        </div>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
            <div class="modular-wrap">
              <div class="modular-header">
                <span class="title"></span>
              </div>
              <div class="modular-body" style="padding-bottom: 0">
                <p class="statistical-info">21 1</p>
                <ul class="alarm-reminder-list">
                  <li>
                    <span class="name"></span>
                    <span class="time">10:00:00</span>
                    <a href="javascript:;" class="handle-btn"> <i class="el-icon-arrow-right"></i></a>
                  </li>
                  <li>
                    <span class="name"></span>
                    <span class="time">10:00:00</span>
                    <a href="javascript:;" class="handle-btn"> <i class="el-icon-arrow-right"></i></a>
                  </li>
                </ul>
              </div>
            </div>
            <div class="modular-wrap">
              <div class="modular-header">
                <span class="title"></span>
              </div>
              <div class="modular-body" style="padding-bottom: 0">
                  <ul class="warning-reminder-list">
                    <li>
                      <span class="name">10</span>
                      <a href="javascript:;" class="operation-btn"> <i class="el-icon-arrow-right"></i></a>
                    </li>
                    <li>
                      <span class="name">6</span>
                      <a href="javascript:;" class="operation-btn"> <i class="el-icon-arrow-right"></i></a>
                    </li>
                  </ul>
              </div>
            </div>
            <div class="modular-wrap">
              <div class="modular-header">
                <span class="title"></span>
              </div>
              <div class="modular-body">
                <div class="operation-title clear">
                  <span class="title"> 10000000000</span>
                  <div class="btn-wrap">
                    <a class="operation-btn" href="javascript:;">  </a>
                    <a class="operation-btn" href="javascript:;">  </a>
                  </div>
                </div>
                <p class="operation-text">
                  <span> 2018-11-24 08:00:00</span>
                  <span></span>
                </p>
                <p class="operation-text">6</p>
              </div>
            </div>
          </div>
        </el-scrollbar>
      <el-dialog
        width="780px"
        title="-"
        :append-to-body = 'true'
        custom-class = "no-padding-dialog"
        :visible.sync="siteDetails" >
        <site-details-info></site-details-info>
      </el-dialog>


    </div>
</template>

<script>
  import siteDetailsInfo from './siteDetailsInfo'
    export default {
        name: "overviewControl",
        components:{
          siteDetailsInfo
        },
        data(){
          return{
            siteDetails: false,
            //
            auditData:[
              {
              value:'7',
              company:'',
              factor:'PH',
              grade:1
            },{
              value:'20',
              company:'',
              factor:'',
              grade:2
            },{
              value:'27.2',
              company:'NTU',
              factor:'',
              grade:3
            },{
              value:'0.13',
              company:'mg/l',
              factor:'',
              grade:4
            },{
              value:'0.13',
              company:'mg/l',
              factor:'',
              grade:5
            },{
              value:'0.13',
              company:'mg/l',
              factor:'',
              grade:6
            },{
              value:'0.2',
              company:'mg/l',
              factor:'',
              grade:6
            },{
              value:'0.2',
              company:'S/cm',
              factor:'',
              grade:1
            },{
              value:'0.2',
              company:'mg/l',
              factor:'',
              grade:1
            }
            ],
            siteInfo:{
              stationName:'-',
              area:{
                areaName:'',
              },
              basinn:{
                basinnName:'-',
              },
              buildTime:'-',
              stationtype:'-'
            }
          }
        },
        computed: {
          siteMn: {
            get: function() {
              return this.$store.state.siteMn.stationMn;
            }
          }
        },
        methods:{
          setGradeClass(item,type){
            let grade = item.grade;
            let cls = '';
            let val = '';
            switch (grade) {
              case 1:
                cls = 'grade-a';
                val = '';
                break;
              case 2:
                cls = 'grade-a';
                val = '';
                break;
              case 3:
                cls = 'grade-b';
                val = '';
                break;
              case 4:
                cls = 'grade-c';
                val = '';
                break;
              case 5:
                cls = 'grade-d';
                val = '';
                break;
              case 6:
                cls = 'grade-e';
                val = '';
                break;
            }
            if(type === 'val'){
              return val;
            }else{
              return cls;
            }

          },
          //
          auditDate(){
            this.$confirm('','',{
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning',
              center: true
            }).then(()=>{
              this.$router.push('/dataAudit/data/threelevelAudit');
            }).catch(()=>{

            });
          },
          //
          getBaseInfo(){
            this.$http.get('/SiteManagement/station/stationInfoByMn',{
              params:{
                mn: this.siteMn
              }
            }).then((res)=>{
              console.log(res);
              if(res.data.code === 0){
                this.siteInfo = res.data.content.info;
              }
            }).catch((error)=>{
              console.log(error);
            })
          }
        },
        mounted(){

        },
        created(){
          this.getBaseInfo();
        }
    }
</script>

<style scoped lang="less">
.overview-control{
  height: 466px;
}
.link-btn{
  float: right;
  color: #999999;
  &:hover{
    color: #166bce;
  }
  &:hover{
    color: #0f509c;
  }
}

.modular-wrap{
  border: 1px solid #dce9fa;
  margin-bottom: 8px;
  &:last-child{
    margin-bottom: 0;
  }
  .modular-header{
    height: 37px;
    line-height: 36px;
    border-bottom: 1px solid #dce9fa;
    padding: 0 16px;
    .title{
      font-size: 14px;
      &:before{
        content: '';
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;
        width: 3px;
        height: 10px;
        background: #166bce;
        margin-right: 8px;
      }
    }
  }
  .modular-body{
    min-height: 100px;
    padding: 16px;
  }
  .base-info{
    float: left;
    width: calc(100% - 134px);
    .base-site-icon{
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 3px;
      width: 20px;
      height: 29px;
      background: url("../../assets/images/state-icon.png") no-repeat;
      margin-right: 8px;
    }
    .base-site-name{
      font-size: 16px;
      margin-right: 14px;
    }
    .details-ben{
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 3px;
      border: 1px solid #9cb0c9;
      border-radius: 5px;
      font-size: 12px;
      color: #778699;
      padding: 0 6px;
      &:hover{
        color: #166bce;
        border: 1px solid #166bce;
      }
      &:active{
        color: #fff;
        border: 1px solid #166bce;
        background: #166bce;
      }
    }
    .base-info-list{
      &>li{
        float: left;
        width: 33.3%;
        line-height: 26px;
      }
    }
  }
  .secondary-title{
    font-weight: 700;
    padding-left: 20px;
    margin-bottom: 10px;
    background: url("../../assets/images/list-icon.png") no-repeat left center;
  }
  .secondary-menu{
    .item{
      display: inline-block;
      vertical-align: top;
      border: 1px solid #cad9eb;
      height: 30px;
      line-height: 28px;
      padding:0 15px;
      margin-bottom: 7px;
    }
  }
  .base-img{
    float: right;
    width: 134px;
    height: 100px;
  }
  .alarm-reminder-list{
    &>li{
      border-bottom: 1px solid #dce9fa;
      line-height: 44px;
      padding-left: 8px;
      .name{
        display: inline-block;
        vertical-align: top;
        padding-left: 20px;
        width: 240px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        background: url("../../assets/images/dian_aa_icon.png") no-repeat left center;
      }
      .handle-btn{
        float: right;
        color: #999999;
        &:hover{
          color: #166bce;
        }
        &:active{
          color: #0f509c;
        }
      }
    }
    &>li:last-child{
      border: none;

    }
  }
  .warning-reminder-list{
    &>li{
      border-bottom: 1px solid #dce9fa;
      line-height: 44px;
      padding-left: 8px;
      .name{
        display: inline-block;
        vertical-align: top;
        padding-left: 20px;
        width: 500px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        background: url("../../assets/images/list-icon.png") no-repeat left center;
      }
      .operation-btn{
        float: right;
        color: #999999;
        &:hover{
          color: #166bce;
        }
        &:active{
          color: #0f509c;
        }
      }
    }
    &>li:last-child{
      border: none;

    }
  }
  .operation-title{
    padding-bottom: 6px;
    .title{
      padding-left: 20px;
      background: url("../../assets/images/list-icon.png") no-repeat left center;
    }
    .btn-wrap{
      float: right;
    }
    .operation-btn{
      color: #166bce;
      padding: 0 16px;
      position: relative;
      &:after{
        content:'';
        position: absolute;
        top: 4px;
        right: 0;
        width: 1px;
        height: 12px;
        background: #dce9fa;
      }
      &:hover{
        color: #1155a3;
      }
    }
  }
  .operation-text{
    padding-left: 20px;
    line-height: 28px;
    &>span{
      display: inline-block;
      vertical-align: middle;
      margin-bottom: 3px;
      width: calc(50% - 4px);
    }
  }
  .monitoring-info{
    border-bottom: 1px solid #DCE9FA;
    padding: 14px 16px;
    .monitoring-title{
      font-size: 15px;
      .title{
        color: #333333;
      }
    }
    .monitoring-text{
      margin-bottom: 3px;
      padding-left: 26px;
      line-height: 28px;
      &>span{
        display: inline-block;
        vertical-align: middle;
        margin-right: 48px;
      }
    }
    .toolbar{
      float: right;
    }
  }
  .audit-status{
    padding: 14px 16px 0;
    .audit-title{
      font-size: 15px;
      .title{
        color: #333333;
        .audit-icon{
          display: inline-block;
          vertical-align: middle;
          width: 19px;
          height: 18px;
          background: url("../../assets/images/index/s_shenghe_icon.png") no-repeat;
        }
      }
    }
    .audit-wrap{
      margin-bottom: 3px;
      padding-left: 26px;
      line-height: 28px;
      &>span{
        display: inline-block;
        vertical-align: middle;
        margin-right: 48px;
      }
      .state-icon{
        display: inline-block;
        vertical-align: middle;
        margin-bottom: 3px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: #c1ccd9;
        margin-right: 6px;
      }
      .state-normal{
        background: #36c25e;
      }
    }
  }
  .audit-data-wrap{
    padding: 0 16px 0;
    .audit-data-title{
      .title{
        padding-left: 20px;
        background: url("../../assets/images/list-icon.png") no-repeat left center;
        font-size: 12px;
      }
      .toolbar{
        float: right;
      }
    }
    .audit-data-content{
      .audit-data-list{
        &>li{
          width: 20%;
          text-align: center;
          float: left;
          padding: 10px 0;
          .value{
            font-size: 16px;
            color: #333;
            font-weight: 700;
          }
          .company{
            font-size: 12px;
          }
          .audit-text{
            line-height: 28px;
          }
        }
      }
    }

  }
}
  .abnormal-icon,.site-normal-icon,.site-offline-icon{
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 3px;
    width: 21px;
    height: 21px;
  }
  .abnormal-icon{
      background: url("../../assets/images/index/abnormal-icon.png") no-repeat;
  }
  .site-normal-icon{
    background: url("../../assets/images/index/normalSite.png") no-repeat;
  }
  .site-offline-icon{
    background: url("../../assets/images/index/siteOffline.png") no-repeat;
  }
  .water-quality{
    display: inline-block;
    vertical-align: middle;
    width: 18px;
    height: 18px;
    margin-bottom: 3px;
  }
  .typeA{
    background: url("../../assets/images/water_quality_01.png") no-repeat;
    background-size: 100% 100%;
  }
  .typeB{
    background: url("../../assets/images/water_quality_02.png") no-repeat;
    background-size: 100% 100%;
  }
  .typeC{
    background: url("../../assets/images/water_quality_03.png") no-repeat;
    background-size: 100% 100%;
  }
  .typeD{
    background: url("../../assets/images/water_quality_04.png") no-repeat;
    background-size: 100% 100%;
  }
  .typeE{
    background: url("../../assets/images/water_quality_05.png") no-repeat;
    background-size: 100% 100%;
  }
  .grade-icon{
    display: inline-block;
    vertical-align: middle;
    width: 10px;
    height: 11px;
    margin: 0 3px 3px;
  }
  .grade-a{
    background: url("../../assets/images/index/s_yierlei_icon.png") no-repeat;
  }
  .grade-b{
    background: url("../../assets/images/index/s_sanlei_icon.png") no-repeat;
  }
  .grade-c{
    background: url("../../assets/images/index/s_silei_icon.png") no-repeat;
  }
  .grade-d{
    background: url("../../assets/images/index/s_wulei_icon.png") no-repeat;
  }
  .grade-e{
    background: url("../../assets/images/index/s_liewulei_icon.png") no-repeat;
  }
</style>



